.Post {
    border-radius: 3px;
    border: 1px solid #e6e6e6;
    background-color: #fff;
    margin-bottom: 60px;
    margin-top: 20px;
    
    width:200px;
    
    
  }
  .Post-user {
    padding: 16px;
    -ms-flex-align: center;
        align-items: center;
  }
  .Post-user-avatar {
    width: 30px;
    height: 30px;
  }
  .Post-user-avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .Post-user-nickname {
    margin-left: 12px;
    font-family: 'PT Sans', sans-serif;
    font-weight: bold;
  }
  .Post-image-bg {
    background-color: #efefef;
  }
  .Post-image img {
    display: block;
    width: 100%;
  }
  .Post-caption {
    padding: 16px 16px;
    height: auto;
  }
  .likes {
    padding: 6px 6px;
    height: auto;
  }
  .Post-caption strong {
    font-family: 'PT Sans', sans-serif;
    font-weight: bold;
  }
  .vjs-fade-out {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
  .beechkeliye {
    border:2px solid rgb(188, 213, 245);
  }


